import { useDispatch } from "react-redux";
import { CountryListProps } from "../interface/country";
import { useEffect, useState } from "react";
import { setSelected } from "../store/country-slice";
import { ListGroup } from "react-bootstrap";

const CountriesList: React.FC<CountryListProps> = ({ countries }) => {

  const dispatch = useDispatch()
  const [bgColorIndex, setBgColorIndex] = useState(0);
  const bgColor = ['#FFC0CB', "#FF00FF", "#20B2AA", "#FF0000", "#FFA500"];


  const handleSelectClick = (countryCode: string) => {
    dispatch(setSelected(countryCode))
  };

  return (
    <ListGroup>
      {countries.map((c, index) =>
        <ListGroup.Item as="li"
          key={c.code}
          action
          style={{ backgroundColor: c.isSelected ? bgColor[bgColorIndex] : "initial" }}

          // style={{ backgroundColor: c.isSelected ? bgColor[bgColorIndex % bgColor.length] : "initial" }}
          onClick={() => {
            handleSelectClick(c.code);
            setBgColorIndex(bgColorIndex + 1);
          }}
        >
          {c.emoji} {c.name}
        </ListGroup.Item>
      )}
    </ListGroup>
  )
}

export default CountriesList