import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { ICountry, ICountryState } from '../interface/country';

const initialState: ICountryState = {
    countries: [],
    searchText: '',
    filteredCountries: [],
};

export const countrySlice = createSlice({
    name: "country",
    initialState,
    reducers: {
        addCountry: (state, action: PayloadAction<ICountry[]>) => {
            state.countries.push(...action.payload);
        }, 
        setSearchText: (state, action: PayloadAction<string>) => {
                state.searchText = action.payload;
                state.filteredCountries = state.countries.filter(country =>
                    country.name.toLowerCase().includes(action.payload.toLowerCase())
                );
            
        },
        setSelected:(state,action:PayloadAction<string>)=>{
            state.countries.forEach(country => {
                if(country.code === action.payload){
                    country.isSelected=!country.isSelected;
                    
                }else{
                    country.isSelected = false;
                }
            });
        },
      


    }
});

export const { addCountry ,setSearchText,setSelected} = countrySlice.actions;

export default countrySlice.reducer;
